html {
  --main-color: #00acdc;
}

.body {
  min-width: 1024px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  align-items: center;
  height: 100%;
  display: flex;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav li:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  text-align: center;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--main-color);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin-top: -4px;
  display: block;
}

